
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapState,mapGetters,mapMutations} from 'vuex';
import VmBackTop from "vue-multiple-back-top";
export default {
name: 'app',
components:{
Tab,
TabItem,
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet,
VmBackTop,
Badge
},
computed:{//
...mapGetters(['showMoreMenus']),
...mapState(['topics','unLoginedMoreMenus','loginedMoreMenus','isLogined']),
isShowMenus:{
get:function(){
return this.$store.state.isShowMenus;
},
set:function(nval){

this.$store.commit('mutationIsShowMenus',nval)
}
}
},
data:function(){
return {
badgeText:''
}

},

created:function(){

var loginStatus= window.localStorage.getItem('loginStatus');
if (loginStatus) {

this.mutationLogin();
this.mutationLoginname(window.localStorage.getItem('loginname'));
this.mutationAccessToken(window.localStorage.getItem("accesstoken"));
this.mutationAuthorId(window.localStorage.getItem('author_id'));
}



if (this.isLogined) {

this.getUnreadMsgCount();

var app =this;
window.setInterval(function(){
app.getUnreadMsgCount();
},5*60*1000);
}



},
methods:{


routetohome:function(){
this.$router.replace('/')
},


getUnreadMsgCount:function(){
var app=this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(response){
if (response.data.success) {
app.badgeText=response.data.data;
}
})
},
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationAuthorId']),

showMenus:function(){


this.isShowMenus=true;
},

clickMoreMenus(menukey,menuitem){
console.log(menukey);

this.$router.push('/'+menukey)
}
}


}



